import '../assets/sass/Modal.scss';
import error from '../assets/img/icons/eliminar.png';

const Modal = (props) => {
  const esconderModal = () => {
    const modal = document.getElementById('modal');
    modal.classList.remove('show');
  }

  return (
    <div className="modal" id="modal">
      <div className="centro">
        <div className="icono">
          <img src={error} alt="Icono error"/>
        </div>
        <div className="titulo"></div>
        <div className="texto"></div>
        <div className="btns">
          <button className="aceptar" onClick={esconderModal}>Aceptar</button>
        </div>
      </div>
    </div>
  );

}

export default Modal;