import '../assets/sass/Form.scss';
import { Link } from 'react-router-dom';
import { store } from '../firebaseconfig';
import { useState, useEffect } from 'react';
// import Api from '../Api/index';

import Producto from './Producto';
import arrojarError from './funciones.js';

const Pedido = (props) => {
  const [productos, setProductos] = useState([]);
  const [total, setTotal] = useState(0);
  const [nombre, setNombre] = useState('');
  const [fecha, setFecha] = useState('');
  const [hora, setHora] = useState('10:00');
  const id_evento = props.match.params.id_evento;
  const id_pedido = props.match.params.id_pedido;

  useEffect( () => {
    store.collection('eventos').get()
      .then(res => {
        const { docs } = res;
        const evento = docs[id_evento].data();
        if(!id_pedido){
          setFecha(evento.fecha);
        }else{
          const pedido = evento.pedidos[id_pedido];
          setFecha(pedido.fecha);
          setNombre(pedido.nombre);
          setHora(pedido.hora);
          setProductos(pedido.productos);
        }
      })
  }, [id_pedido, id_evento]);

  useEffect( () => {
    let total = 0;
    productos.map( prod => total += parseFloat(prod.precio))
    setTotal(total);
  }, [productos])

  const validar = () => {
    if(!nombre || !fecha || !hora){
      arrojarError({
        mss: 'Los campos son obligatorios',
        tipo: 'error',
      });
      return false;
    }
    if(productos.length <= 0){
      arrojarError({
        mss: 'Debes agregar al menos un producto.',
        tipo: 'error',
      });
      return false;
    }
    return true;
  }
  const GuardarEnNube = (pedido) => {
    
    return new Promise( (resolve, reject) => {
      store.collection('eventos').get()
      .then(response => {
        const arrayEventos = response.docs.map( (ev, index) => ({id: ev.id,...ev.data()}) );
        let nuevoEvento = arrayEventos[id_evento];
        let existePedido = false;
        nuevoEvento.pedidos = nuevoEvento.pedidos.map( (e, index) => {
          if(parseInt(id_pedido) === index){
            e = pedido
            existePedido = true;
          }
          return e;
        })
        if(!existePedido) nuevoEvento.pedidos = [pedido, ...nuevoEvento.pedidos];
        return nuevoEvento;
      })
      .then( evento => {
        const id = evento.id;
        delete evento.id;
        store.collection("eventos").doc(id).set(evento)
        .then(() => {
            console.log("Document successfully written!");
            resolve(true);
        })
        .catch((error) => {
            console.error("Error writing document: ", error);
            reject(error);
        });
      })
    })
    
  }
  const guardar = (e) => {
    e.preventDefault()
    if(!validar())return;

    let pedido = {nombre, fecha, hora, total, estatus: false, pagado: false, productos}

    GuardarEnNube(pedido)
      .then( () => {
        console.log("se guardo con √©xito");
        props.history.push('/evento/'+id_evento)
      })
      .catch( e => {
        console.log(e);
      })
  }
  const a√±adirProducto = (e) => {
    e.preventDefault()
    const tipo = document.getElementById('tipo');
    const tama√±o = document.getElementById('tama√±o');
    const precio = document.getElementById('precio');
    const relleno = document.getElementById('relleno');
    const decoracion = document.getElementById('decoracino');
    const nombre_producto = document.getElementById('nombre_producto');

    if(precio.value.length <= 0){
      arrojarError({
        mss: 'Ingresa un precio.',
        tipo: 'error',
      });
      return false;
    }
    const nuevoProd = {
      nombre: nombre_producto.value,
      tama√±o: tama√±o.value,
      tipo: tipo.value,
      relleno: relleno.value,
      decoracion: decoracion.value,
      precio: precio.value,
      entregado: false,
      pagado: false
    }

    nombre_producto.value = 'Pastel';
    tipo.value = 'Vainilla';
    relleno.value = 'Fresa';
    tama√±o.value = 'Mini';
    precio.value = '';
    decoracion.value = '';

    setProductos([...productos, nuevoProd]);
  }
  const quitarProducto = (id) => {
    let oldProducts = [...productos]
    oldProducts.splice(id, 1);
    setProductos(oldProducts);
  } 
  return (
    <div className="form_pedido esquina">
      <Link className="link-regreso" to={`/evento/${id_evento}`}>Regresar</Link>
      <h2>Nuevo pedido</h2>
      <form>
        <div className="field">
          <label>Nombre</label>
          <input 
            placeholder="Ingresa nombre del evento" 
            value={nombre}
            onChange={ (e) => setNombre(e.target.value)}/>
        </div>
        <div className="field_group">
          <div className="field">
            <label>Fecha</label>
            <input type="date" placeholder="Ingresa fecha del evento"
              value={fecha}
              onChange={ (e) => setFecha(e.target.value)}
            />
          </div>
          <div className="field">
            <label>Hora</label>
            <input 
              type="time" placeholder="Ingresa fecha del evento"
              value={hora}
              onChange={ (e) => setHora(e.target.value)}
            />
          </div>
        </div>

        <span className="division">A√±adir Producto</span>

        <div className="area-productos">
          <div className="form-producto">
            <div className="field_group">
              <div className="field">
                  <label>Nombre</label>
                  <select id="nombre_producto">
                    <option value="Pastel">Pastel</option>
                    <option value="Chees Cake">Cheese Cake</option>
                  </select>
              </div>
              <div className="field">
                <label>Tama√±o</label>
                <select id="tama√±o">
                  <option value="Mini">Mini</option>
                  <option value="Chico">Chico</option>
                  <option value="Mediano">Mediano</option>
                  <option value="Cuadrado">Cuadrado</option>
                </select>
              </div>
            </div>
            <div className="field_group">
              
              <div className="field">
                <label>Tipo</label>
                <select id="tipo">
                  <option value="Vainilla">Vainilla 3 leches</option>
                  <option value="Vainilla">Chocolate 3 leches</option>
                  <option value="Mantequilla">Pan Mantequilla</option>
                </select>
              </div>
              <div className="field">
                <label>Relleno</label>
                <select id="relleno">
                  <option value="Fresa">Fresa</option>
                  <option value="Mango">Mango</option>
                  <option value="Cajeta">Cajeta/Nu√©z</option>
                  <option value="Crunch">Chocolate</option>
                  <option value="Crunch">Nu√©z</option>
                  <option value="Crunch">Almendras</option>
                  <option value="Crunch">Galleta Oreo</option>
                </select>
              </div>
            </div>
            <div className="field">
            <label>Precio</label>
              <input id="precio" type="number" placeholder="Precio"/>
            </div>
            <div className="field">
              <label>Decoraci√≥n</label>
              <textarea id="decoracino" rows="3" cols=""  type="text" placeholder="Ingresa una descripci√≥n"/>
            </div>
            <button className="tiny" onClick={a√±adirProducto}>A√±adir producto</button>
          </div>
          <span className="division">Productos</span>
          <div className="tabla-productos">
            { productos.length <= 0 ? <p>No has a√±adido productos a√∫n</p> :
              productos.map( (prod, index) => <Producto key={index} index={index} {...prod} handleClickQuitar={quitarProducto}/>)
            }
          </div>
        </div>
        <div className="total">Total: {total}</div>
        <button onClick={guardar}>Guardar pedido</button>
      </form>
    </div>
  );
}

export default Pedido;