import {BrowserRouter as Router, Redirect, Route, Switch} from 'react-router-dom';

import Home from '../components/Home';
import Evento from '../components/Evento.jsx';
import FormEvento from '../components/FormEvento';
import FormPedido from '../components/FormPedido';
import Error404 from '../components/Error404';
import Login from '../components/Login';
import Productos from '../components/Productos';
import Modal from '../components/Modal';

export default function Routers(){
  const isAuth = () => {
    const user = localStorage.getItem('user');
    if(user !== null){
      return true;
    }
    return false;
  }

  const MiddleWareAuth = props => {
    return isAuth() ?
    <Route {...props} /> :
    <Redirect to="/login"/>
  }

  return (
    <div className="App">
      <Modal/>
      <Router>
          <Switch>
            <MiddleWareAuth exact path="/levanta-pedido" component={Home}/>
            <MiddleWareAuth path="/evento/:id" component={Evento}/>
            <MiddleWareAuth path="/nuevo_evento" component={FormEvento}/>
            <MiddleWareAuth path="/ver_pedido/:id_evento/:id_pedido" component={FormPedido}/>
            <MiddleWareAuth path="/nuevo_pedido/:id_evento" component={FormPedido}/>
            <MiddleWareAuth path="/productos/:id_evento/:id_pedido" component={Productos}/>
            <Route path="/login" component={Login}/>
            <Route path="*" component={Error404}/>
          </Switch>
        </Router>
    </div>
  );
}