
import { useEffect, useState } from 'react';
import { store } from '../firebaseconfig';
import { Link } from 'react-router-dom';

import '../assets/sass/Productos.scss';

export default function Productos(props){
  const {id_evento, id_pedido} = props.match.params
  const [pedido, setPedido] = useState({});
  const [isLoading, setIsLoading] = useState(true);

  useEffect( () => {
    if(id_pedido){
      store.collection('eventos').get()
      .then( (res) => {
        const evento = res.docs[id_evento].data();
        const pedido = evento.pedidos[id_pedido];
        setPedido(pedido);
        setIsLoading(false);
      })
    }
  }, [id_evento, id_pedido]);
  const est = () => {
    return pedido.estatus ? 
    <div className="terminado">Terminado</div>:
    <div className="pendiente">Pendiente</div>
  }
  return(
    isLoading ? <div className="contenedor-loader">Cargando<div className="lds-ellipsis"><div></div><div></div><div></div><div></div></div></div> :
    <div className="productos esquina">
      <Link className="link-regreso" to={`/evento/${id_evento}`}>Regresar</Link>
      <h2>PEDIDO</h2>
      <h2 className="titulo">{pedido.nombre}</h2>
      <div className="fecha_hora">
        <div>Fecha: {pedido.fecha}</div>
        <div>Hora: {pedido.hora}</div>
      </div>
      {est()}

      <div className="lista-productos ">
        <table className="tabla">
          <thead>
            <tr>
              <th>Nombre</th>
              <th>Tipo</th>
              <th>Tama√±o</th>
              <th>Relleno</th>
              <th>Precio</th>
              <th>Decoraci√≥n</th>
            </tr>
          </thead>
          <tbody>
          { pedido.productos.length <= 0 ? <tr><td colSpan="4">No hay pedidos</td></tr> :
          pedido.productos.map( (pedido, index) => {
              return <tr key={index} >
                  <td>{pedido.nombre}</td>
                  <td>{pedido.tipo}</td>
                  <td>{pedido.tama√±o}</td>
                  <td>{pedido.relleno}</td>
                  <td>$ {pedido.precio}</td>
                  <td>{pedido.decoracion}</td>
                  
                </tr>
              
            })
          }
          </tbody>
        </table>
      </div>
    </div>
  );
}