
const arrojarError = (data) => {
  const modal = document.getElementById('modal');

  const texto = data.mss ? data.mss : 'Sin texto';

  let tipo = '';
  switch (data.tipo) {
    case 'error':
      tipo = 'Error'
      break;
  
    default:
      tipo = 'sin tipo'
      break;
  }

  modal.classList.add('show')
  modal.querySelector('.titulo').innerHTML = tipo;
  modal.querySelector('.texto').innerHTML = texto;
}

export default arrojarError;