
import borrar from '../assets/img/icons/eliminar.png'
export default function Producto({nombre, tipo, precio, relleno, decoracion, handleClickQuitar,index}){
  return(
    <div id={index} className="prod">
      <div>{nombre}</div>
      <div>{tipo}</div>
      <div>{relleno}</div>
      <div>${precio}</div>
      <div className="dec">{decoracion}</div>
      <div className="acciones"><img onClick={() => handleClickQuitar(index)} src={borrar} alt="Icono borrar"/></div>
    </div>
  );
}