import React, { useEffect, useContext, useState} from 'react';
import { Link } from 'react-router-dom';
import { store, auth } from '../firebaseconfig';

import '../assets/sass/Home.scss';
import '../assets/sass/Tabla.scss';

import iconoAgregar from '../assets/img/icons/agregar_blanco.png';
import iconoEliminar from '../assets/img/icons/eliminar.png';
import iconoMostrar from '../assets/img/icons/mostrar.png';
import iconoMenu from '../assets/img/icons/menu.png';
import iconoCerrar from '../assets/img/icons/cerrar.png';

// import eventos from '../Api/index';
import EventoContext from '../context/index';

const Home = (props) => {
  const { isAuth, setIsAuth} = useContext(EventoContext);

  const [eventos, setEventos] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [update, setUpdate] = useState(false);

  useEffect(() => {
    
    const getEventos = async () => {
      setIsLoading(true);
      const {docs} = await store.collection('eventos').get();
      const arrayEventos = docs.map( (ev, index) => ({id: index, uid: ev.id, ...ev.data()}) );
      setEventos(arrayEventos);
      setIsLoading(false);
    }
    if(isAuth)getEventos()
  }, [isAuth, update]);


  const eliminar = async id => {
    setIsLoading(true);
    let newEventos = [...eventos];
    const evento = newEventos.splice(id, 1);

    if(evento[0]){
      store.collection('eventos').doc(evento[0].uid).delete().then(() => {
        console.log('success');
        setUpdate(!update);
        setIsLoading(false);
      }).catch( e => {
        console.log('ocurri√≥ un error', e);
        setIsLoading(false);
      })
    }

  }
  const cerrar = (id) => {
    setIsLoading(true);
    let copyEvento = {...eventos[id]}
    copyEvento.estatus = true;
    const uid = copyEvento.uid;
    delete copyEvento.id;
    delete copyEvento.uid;

    store.collection('eventos').doc(uid).set(copyEvento)
    .then( res => {
      console.log('sucess');
      setUpdate(!update);
      setIsLoading(false);
    })
    .catch(e => {
      console.log("ocurrio un error", e);
      setIsLoading(false);
    })
  }

  const activarMenu = (id) =>{
    let todasHamburuesas = document.querySelectorAll('table .hamburguesa')
    todasHamburuesas.forEach((e) => { 
      if(parseInt(e.parentNode.parentNode.id) !== id){
        e.classList.remove('on')
      }
    })
    let tr = document.getElementById(id)
    tr.querySelector('.hamburguesa').classList.toggle('on')
  }

  const CerrarSesion = () => {
    auth.signOut();
    setIsAuth(false);
    localStorage.removeItem('user');
    console.log(props.history.push('/login'));
  } 

  return (
    isLoading ? <div className="contenedor-loader">Cargando<div className="lds-ellipsis"><div></div><div></div><div></div><div></div></div></div> :
    <div className="home esquina">
      <button onClick={CerrarSesion} className="end-session link-regreso">Cerrar Sesi√≥n</button>
      <h2 className="titulo">Eventos</h2>
      <div className="areaEventos">
        <h2 className="titulo">
          Lista de eventos 
          <Link to='/nuevo_evento' className="btn_nuevo"> <img src={iconoAgregar} alt="Icono Agregar"/></Link>
        </h2>
    
        <div className="listaEventos">
          <table className="tabla">
            <thead>
              <tr>
                <th>Nombre</th>
                <th>Fecha</th>
                <th>Estatus</th>
                <th>Acciones</th>
              </tr>
            </thead>
            <tbody>
            { eventos.length <= 0 ? <tr><td colSpan="4">No hay eventos</td></tr> :
              eventos.map( (evento) => {
                return <tr key={evento.id} id={evento.id}>
                    <td>{evento.nombreEvento}</td>
                    <td>{evento.fecha}</td>
                    {evento.estatus ? <td className="cerrado">Cerrado</td>  
                      : <td className="abierto">Abierto</td>
                    }
                    <td>
                      <div className="hamburguesa" onClick={() => activarMenu(evento.id)}>
                        <div><img src={iconoMenu} alt="Icono menu"/></div>
                        <div className="menu">
                          <ul>
                            <li className="mostrar">
                              <Link to={`/evento/${evento.id}`} className="btn_acciones"><img src={iconoMostrar} alt="Icono mostrar"/>Ver</Link>
                            </li>
                            {!evento.estatus &&
                              <React.Fragment>
                                <li className="eliminar" onClick={() => cerrar(evento.id)}>
                                  <div className="btn_acciones">
                                    <img src={iconoCerrar} alt="Icono borrar"/>
                                  </div>
                                  Cerrar
                                </li>
                                <li className="eliminar" onClick={() => eliminar(evento.id)}>
                                  <div className="btn_acciones">
                                    <img src={iconoEliminar} alt="Icono borrar"/>
                                  </div>
                                  Eliminar
                                </li>
                              </React.Fragment>
                            }
                          </ul>
                        </div>
                      </div>
                    </td>
                  </tr>
                
              })
            }
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}
export default Home;