import { useState, useContext} from 'react';
import { useHistory } from 'react-router-dom';
import {auth} from '../firebaseconfig';

import '../assets/sass/Login.scss';
import EventoContext from '../context/index';

export default function Login(){
  const { setIsAuth } = useContext(EventoContext);
  const history = useHistory();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [msgError, setMsgError] = useState(null);

  const validarCampos = () => {

    setMsgError(null);
    if(!password.trim() || !email.trim()){
      setMsgError('Llena todos los campos.');
      return false;
    }
    return true;
  }

  const login = (e) => {
    e.preventDefault();
    validarCampos();
    if(!validarCampos()) return;

    auth.signInWithEmailAndPassword(email, password)
    .then( response => {
      if(response.user.uid){
        localStorage.setItem('user', response.user.uid)
      }
      setIsAuth(true);
      setEmail('');
      setPassword('');
      history.push('/levanta-pedido');
    })
    .catch( e => {
      console.log(e);
      if(e.code === 'auth/invalid-email'){
        setMsgError('Correo invalido');
      }else if(e.code === 'auth/user-not-found'){
        setMsgError('No existe este usuario.');
      }else if(e.code === 'auth/wrong-password'){
        setMsgError('Contrase√±a incorrecta');
      }
    })

  }
  return(
    <div className="login esquina">
      <h2>Iniciar Sesi√≥n</h2>
      <form onSubmit={login}>
        <div className="field">
        <label>Correo</label>
          <input 
            type="email" 
            id="email" 
            placeholder="Ingresa el correo"
            value={email}
            onChange={ (e) => setEmail(e.target.value)}
          />
        </div>
        <div className="field">
          <label>Contrase√±a</label>
          <input 
            type="password" 
            id="password" 
            placeholder="Ingresa contrase√±a"
            value={password}
            onChange={ (e) => setPassword(e.target.value)}
          />
        </div>
        <button type="submit" > Ingresar</button>
        {
          msgError && <div className="error-mesagge">{ msgError}</div>
        }
        
      </form>
    </div>
  );
}