import '../assets/sass/Form.scss';
import { store } from '../firebaseconfig';
import { Link } from 'react-router-dom';
import { useState } from 'react';

import arrojarError from './funciones.js';



const Evento = (props) => {
  const [nombre, setNombre] = useState('');
  const [fecha, setFecha] = useState('');
  const validar = () => {
    if(!nombre || !fecha){
      arrojarError({
        mss: 'Los campos son obligatorios',
        tipo: 'error',
      });
    }
    return true;
  }

  const guardar = async (e) => {
    e.preventDefault()
    if(!validar())return;

    let evento = {
      nombreEvento: nombre,
      fecha: fecha,
      estatus: false,
      pedidos: []
    }

    try{
      await store.collection('eventos').add(evento);
      setNombre('');
      setFecha('');
      props.history.push('/')
    }catch(e){
      console.log(e);
    }
  }
  return (
    <div className="form_evento esquina">
      <Link className="link-regreso" to='/'>Regresar</Link>
      <h2>Nuevo evento</h2>
      <form>
        <div className="field">
          <label>Nombre</label>
          <input 
            placeholder="Ingresa nombre del evento" 
            value={nombre}
            onChange={ (e) => setNombre(e.target.value)}
          />
        </div>
        <div className="field">
          <label>Fecha</label>
          <input 
            type="date" 
            placeholder="Ingresa fecha del evento"
            value={fecha}
            onChange={(e) => setFecha(e.target.value)}
          />
        </div>

        <button onClick={guardar}>Guardar</button>
      </form>
    </div>
  );
}

export default Evento;