// import { useState } from 'react';
import '../assets/sass/Tabla.scss';
import '../assets/sass/Evento.scss';

import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { store } from '../firebaseconfig';

// import Api from '../Api/index';

import iconoAgregar from '../assets/img/icons/agregar_blanco.png';
import iconoEliminar from '../assets/img/icons/eliminar.png';
import iconoMostrar from '../assets/img/icons/mostrar.png';
import iconoEditar from '../assets/img/icons/editar.png';
import iconoMenu from '../assets/img/icons/menu.png';
import iconoEntregar from '../assets/img/icons/entregar.png';
import iconoPagado from '../assets/img/icons/pagado.png';

const Evento = (props) => {
  
  const { id } = props.match.params;
  // const data = (Api.filter( prop => prop.id === parseInt(id)))[0];

  const [evento, setEvento] = useState([]);
  const [pedidos, setPedidos] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [update, setUpdate] = useState(false);
  const [filtroEstatus, setFiltroEstatus] = useState(2);
  const [filtroPago, setFiltroPago] = useState(2);
  const [bucador, setBuscador] = useState('');

  useEffect( () => {
    const getEventos = async () => {
      setIsLoading(true);
      const {docs} = await store.collection('eventos').get();
      const arrayEventos = docs.map( (ev, index) => ({id: index, uid: ev.id, ...ev.data()}) );
      if(arrayEventos[id]){
        setEvento(arrayEventos[id]);
        setPedidos(arrayEventos[id].pedidos);
      }
      setIsLoading(false);
    }
    getEventos();
  }, [update, id]);

  const eliminar = id => {
    setIsLoading(true);
    const copyEvento = {...evento};
    let newPedidos = [...copyEvento.pedidos];
    newPedidos.splice(id, 1);
    copyEvento.pedidos = newPedidos;
    const uid = copyEvento.uid;
    delete copyEvento.uid;
    delete copyEvento.id;

    store.collection('eventos').doc(uid).set(copyEvento).then(() => {
      console.log('success');
      setUpdate(true);
      setIsLoading(false);
    }).catch( e => {
      console.log('ocurri√≥ un error', e);
      setIsLoading(false);
    })
  }
  
  const activarMenu = (id) =>{
    let tr = document.getElementById(id)
    tr.querySelector('.hamburguesa').classList.toggle('on')
  }

  const buscar = (e) => {
    let dataFiltrada = evento.pedidos.filter( pedido => {
      let pasa = true;

      // filtro busqueda
      let pasaBusqueda = true;
      pasaBusqueda = pasa = pedido.nombre.toLowerCase().includes(bucador.toLowerCase());

      // filtro Pago
      let pasaPago = true;
      if(pasaBusqueda && filtroPago !== '2'){
        pasaPago = pasa = !!parseInt(filtroPago) === pedido.pagado;
      }

      // filtro estatus
      if(pasaBusqueda && pasaPago && filtroEstatus !== '2'){
        pasa = !!parseInt(filtroEstatus) === pedido.estatus;
      }

      return pasa;
    } )

    setPedidos(dataFiltrada);
  }

  const marcarEntregado = (id) => {
    setIsLoading(true);
    const copyEvento = {...evento};
    copyEvento.pedidos[id].estatus = true;

    const uid = copyEvento.uid;
    delete copyEvento.uid;
    delete copyEvento.id;

    store.collection('eventos').doc(uid).set(copyEvento).then(() => {
      console.log('success');
      setUpdate(true);
      setIsLoading(false);
    }).catch( e => {
      console.log('ocurri√≥ un error', e);
      setIsLoading(false);
    })
  }
  const marcarPagado = (id) => {
    setIsLoading(true);
    const copyEvento = {...evento};
    copyEvento.pedidos[id].pagado = true;

    const uid = copyEvento.uid;
    delete copyEvento.uid;
    delete copyEvento.id;

    store.collection('eventos').doc(uid).set(copyEvento).then(() => {
      console.log('success');
      setUpdate(true);
      setIsLoading(false);
    }).catch( e => {
      console.log('ocurri√≥ un error', e);
      setIsLoading(false);
    })
  }

  return (
    isLoading ? <div className="contenedor-loader">Cargando<div className="lds-ellipsis"><div></div><div></div><div></div><div></div></div></div> :
    <div className="evento esquina">
      <Link className="link-regreso" to='/levanta-pedido'>Regresar</Link>
      <h2>{evento.nombreEvento}</h2>
      <p className="fecha-evento">{evento.fecha}</p>

      <div className="buscador">
        <label>Busador</label>
        <input onChange={buscar} type="text" placeholder="Buscar..." onInputCapture={(e) => setBuscador(e.target.value)} value={bucador}/>
      </div>
      
      <h2 className="titulo">
        Pedidos 
        {
          !evento.estatus && <Link to={`/nuevo_pedido/${evento.id}`} className="btn_nuevo"> <img src={iconoAgregar} alt="Icono Agregar"/></Link>
        }
        
      </h2>
      <div className="filtros">
        <div>
          <label>Estatus</label>
          <select onChange={buscar} className="select_default estatus" value={filtroEstatus} onInput={ (e) => setFiltroEstatus(e.target.value)}>
            <option value='2'>seleccionar...</option>
            <option value='0'>Pendiente</option>
            <option value='1'>Entregado</option>
          </select>
        </div>
        <div>
          <label>Pagado</label>
          <select onChange={buscar} className="select_default pagado" value={filtroPago}
          onInput={ (e) => setFiltroPago(e.target.value)}>
            <option value='2'>seleccionar...</option>
            <option value='1'>Pagado</option>
            <option value='0'>No</option>
          </select>
        </div>
      </div>
      <div className="lista-pedidos">
        <table className="tabla">
          <thead>
            <tr>
              <th>Nombre</th>
              <th>Total</th>
              <th>Estatus</th>
              <th>Pagado</th>
              {!evento.estatus && <th>Acciones</th>}
            </tr>
          </thead>
          <tbody>
          { pedidos.length <= 0 ? <tr><td colSpan="5">No hay datos</td></tr>:
            pedidos.map( (pedido, index) => {
              return <tr key={index} id={index}>
                  <td>{pedido.nombre}</td>
                  <td>${pedido.total}</td>
                  {pedido.estatus ? <td className='entregado'>Entregado</td> : 
                    <td className='pendiente'>Pendiente</td>
                  }
                  {pedido.pagado ? <td className='entregado'>Pagado</td> : 
                    <td className='no-pagado'>No</td>
                  }
                  {!evento.estatus &&
                    <td>
                      <div className="hamburguesa" onClick={() => activarMenu(index)}>
                        <div><img src={iconoMenu} alt="Icono menu"/></div>
                        <div className="menu">
                          <ul>
                            <li className="mostrar">
                              <Link to={`/productos/${evento.id}/${index}`} className="btn_acciones"><img src={iconoMostrar} alt="Icono mostrar"/>Ver</Link>
                            </li>
                            {!pedido.pagado && 
                              <li className="entregar" onClick={() => marcarPagado(index)}>
                              <div className="btn_acciones">
                                <img src={iconoPagado} alt="Icono borrar"/>
                              </div>
                              Pagado
                            </li>
                            }
                            { !pedido.estatus &&
                              <React.Fragment>
                                <li className="entregar" onClick={() => marcarEntregado(index)}>
                                  <div className="btn_acciones">
                                    <img src={iconoEntregar} alt="Icono borrar"/>
                                  </div>
                                  Entregdo
                                </li>
                                <li className="eliminar" onClick={() => eliminar(index)}>
                                  <div className="btn_acciones">
                                    <img src={iconoEliminar} alt="Icono borrar"/>
                                  </div>
                                  Eliminar
                                </li>
                                <li className="mostrar">
                                  <Link 
                                    to={`/ver_pedido/${evento.id}/${index}`}    className="btn_acciones"><img src={iconoEditar} alt="Icono editar"/>Editar</Link>
                                </li>
                              </React.Fragment>
                            }
                          </ul>
                        </div>
                      </div>
                    </td>
                  }
                </tr>
              
            })
          }
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default Evento;

